import React from "react";

const QuemSomos = () => {
    return (
        <>
            <section className="section-quem-somos animation" id="quem-somos">
                <h1 className="circle-title-section">Quem Somos</h1>
                    <div className="container-quem-somos">
                        <div className="parte-texto">
                            <p>
                                A Kairós foi gerada primeiramente no coração de Deus e
                                fundada no ano de 2019. Vem crescendo e se
                                desenvolvendo com uma direção excelente de Adalberto
                                e sua equipe altamente qualificada para todos os
                                trabalhos designados. Somos uma empresa de
                                Construção Civil, trabalhamos com construção,
                                reformas, acabamentos, pintura, hidráulica e elétrica.

                                Não medimos esforços para construir os seus sonhos,
                                agindo com confiança e fidelidade para com os seus
                                clientes até os dias de hoje, e pretendendo continuar
                                exercendo a melhor hoje e sempre.
                            </p>

                            <div className="infos">
                                <p>
                                    + De 40 solicitações por mês
                                </p>
                                <p>
                                    + De 30 Clientes atendidos
                                </p>

                            </div>
                        </div>

                        <div className="parte-logo">
                            <div className="logo">
                                <p>_________________________</p>
                                <p>__________________</p>
                                <p>_________</p>
                            </div>
                        </div>
                    </div>
            </section>
        </>
    );
}

export default QuemSomos;